}
.part8 {
  height: 750px;
  background: #FDEAA8;
  padding-top: 60px;
}
.part8 hr {
  margin: 50px auto;
}
.part9 {
  height: 750px;
  background: #FFFDD0;
  padding-top: 60px;
}
.part9 h1 {
  color: #ae3f3f;
}
.part9 li {
  list-style-type: none;
  margin: 15px auto;
}
.part9 li p {
  margin: 0 40px;
  color: gray;
}
.part9 .email {
  display: block;
}
.part9 input,
.part9 textarea {
  height: 40px;
  width: 500px;
  margin: 10px auto;
  border: 2px #ae3f3f solid;
  border-radius: 5px;
  padding-left: 5px;
  font-family: 'Poppins';
}
footer {
  height: 300px;
  background-color: #ae3f3f;
  opacity: 0.902;
}
footer .fa-cutlery {
  font-size: 27px;
  color: #FDEAA8;
}
footer h1 {
  color: #FDEAA8;
}
footer p {
  color: #FDEAA8;
}
footer h4 {
  font-size: 22px;
  color: #FDEAA8;
}
footer .photos {
  width: 70%;
}
footer .photos img {
  width: 70px;
}
